@font-face {
  font-family: "SegoeUIRegular";
  src: url("../src/assets/fonts/SegoeUI/SegoeUIRegular/SegoeUIRegular.eot");
  src: url("../src/assets/fonts/SegoeUI/SegoeUIRegular/SegoeUIRegular.eot?#iefix") format("embedded-opentype"), url("../src/assets/fonts/SegoeUI/SegoeUIRegular/SegoeUIRegular.woff") format("woff"), url("../src/assets/fonts/SegoeUI/SegoeUIRegular/SegoeUIRegular.ttf") format("truetype");
  font-style: normal;
  font-weight: normal; }

@font-face {
  font-family: "SegoeUIBold";
  src: url("./src/assets/fonts/SegoeUI/SegoeUIBold/SegoeUIBold.eot");
  src: url("./src/assets/fonts/SegoeUI/SegoeUIBold/SegoeUIBold.eot?#iefix") format("embedded-opentype"), url("./src/assets/fonts/SegoeUI/SegoeUIBold/SegoeUIBold.woff") format("woff"), url("./src/assets/fonts/SegoeUI/SegoeUIBold/SegoeUIBold.ttf") format("truetype");
  font-style: normal;
  font-weight: normal; }

.well {
  margin-bottom: 0 !important;
  padding: 45px !important; }

::-webkit-input-placeholder {
  color: white; }

@media screen and (max-width: 767px) {
  .picture {
    display: block;
    margin: 0 auto; }
  .btn_wrap {
    padding: 0px !important; } }

.header-block {
  text-align: center; }

.custom-title {
  margin: 0 auto;
  color: #252539;
  font-family: SegoeUIBold;
  display: inline-block;
  width: auto; }

.form-control {
  border-radius: none;
  border: none; }

.add-image {
  border-radius: 10px;
  display: inline-block;
  max-width: 265px !important;
  max-height: 295px !important; }

.picture {
  padding: 0 !important;
  display: inline-block;
  text-align: center;
  background-color: white;
  color: #253939;
  border: 2px dashed #253939;
  border-radius: 10px;
  padding-left: 0;
  width: 270px !important;
  height: 300px !important;
  margin-right: 25px; }

.file-upload {
  cursor: pointer;
  font-family: SegoeUIRegular;
  background-color: #252539;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  color: white;
  border: none;
  width: 190px;
  padding: 5px 0;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  margin: 0 auto;
  overflow: hidden; }

.file-upload input.file-input {
  position: absolute;
  bottom: 0;
  right: 0;
  left: 0;
  margin: 0 auto;
  padding: 0;
  font-size: 20px;
  opacity: 0;
  filter: alpha(opacity=0);
  cursor: pointer; }

.upload_title {
  font-family: SegoeUIRegular;
  margin-top: 50%; }

.file-upload:hover {
  background-color: #3C3C5B; }

.properties_block {
  float: right;
  padding: 0 !important; }

.property, .date-wrap {
  font-family: SegoeUIRegular;
  padding: 15px 10px;
  margin-top: 10px;
  width: 100%;
  border: none;
  color: #253939; }

.btn-default {
  color: #252539 !important;
  line-height: 1.5 !important; }

.event_description-area {
  background-color: #252539 !important;
  color: white;
  border: none;
  border-radius: 0 !important;
  margin: 10px 0 0 0;
  resize: none; }

.category-it {
  display: block; }

.save_btn {
  font-size: 20px;
  font-family: SegoeUIBold;
  color: white;
  background-color: #EFCE4A;
  border: none;
  border-bottom: 2px solid #FFCC00;
  padding: 10px 0;
  width: 100%;
  margin-top: 10px; }

.save_btn:active, .save_btn:visited, .save_btn:focus, .upload_btn:active,
.upload_btn:visited, .upload_btn:focus {
  outline: none;
  -webkit-appearance: none; }

.save_btn:hover {
  background-color: #F1D870;
  border-color: #EFCE4A; }

.save_btn:active {
  border-bottom: none;
  border-top: 2px solid #EFCE4A; }

.btn_wrap {
  float: right; }

.block-wrap {
  margin-top: 25px; }

.date-field {
  display: block;
  background-color: white; }

.close-btn {
  float: right;
  cursor: pointer; }

.glyphicon {
  font-size: 14px !important;
  position: relative !important; }

.btn-link {
  color: #252539 !important; }

.btn-link:hover {
  text-decoration: none; }

.btn-group-sm > .btn, .btn-sm {
  padding: 10px 6px !important; }

.datepick {
  margin-top: 25px; }
